import * as React from "react";
import * as PropTypes from "prop-types";

import NamespaceWrapper, { NamespaceWrapperProps } from "../utils/NamespaceWrapper";

export interface PanelProps extends NamespaceWrapperProps {
    /**
     * The panel's title
     */
    title ?: React.ReactNode;

    /**
     * Set the size of the button
     */
    size ?: "xsmall" | "small" | "medium" | "large" | "xlarge";

    /**
     * Set the color of the button
     */
    level ?: "info" | "warning" | "error" | "success";

    /**
     * Class names to apply to the container
     */
    className ?: string;

    /**
     * Class names to apply to the title
     */
    titleClassName ?: string;

    /**
     * Class names to apply to the content
     */
    contentClassName ?: string;

    /**
     * Can the panel be opened / closed ? (true by default)
     */
    active ?: boolean;

    /**
     * Is the panel opened / closed ? (false by default)
     */
    collapsed ?: boolean;
}

export interface PanelState {
    /**
     * To keep the real state
     */
    collapsed ?: boolean;
}


export default class Panel extends NamespaceWrapper<PanelProps, PanelState> {
    public static displayName = "Panel";

    public static propTypes = {//
        "title": PropTypes.node, //

        // Modifiers
        "className": PropTypes.string, //
        "titleClassName": PropTypes.string, //
        "contentClassName": PropTypes.string, //
        "level": PropTypes.oneOf(["info", "warning", "error", "success"]), //
        "size": PropTypes.oneOf(["xsmall", "small", "medium", "large", "xlarge"]), //

        // States
        "active": PropTypes.bool, //
        "collapsed": PropTypes.bool //
    };

    static defaultProps: PanelProps = {
        active: true,
        collapsed: false
    };

    constructor(props: PanelProps) {
        super(props);

        this.state = {
            collapsed: this.isCollapsed(props)
        };

        this.handleClick = this.handleClick.bind(this);
    }

    public isCollapsed(panelProps: PanelProps): boolean {
        return panelProps.collapsed;
    }

    public componentWillReceiveProps(panelProps: PanelProps) {
        if (this.isCollapsed(panelProps) !== this.isCollapsed(this.props)) {
            this.setState({collapsed: this.isCollapsed(panelProps)});
        }
    }

    public handleClick(e: React.SyntheticEvent<HTMLElement>) {
        if (!this.props.active) {
            return;
        }
        this.setState({collapsed: !this.state.collapsed});
    }

    public render() {
        const className = this.classes("Panel", this.props.level && "Panel--" + this.props.level, this.props.size && "Badge--" + this.props.size, this.state.collapsed && "is-collapsed", this.props.className);
        const titleClassName = this.classes("Panel__title", this.props.titleClassName);
        const contentClassName = this.classes("Panel__content", this.props.contentClassName);

        return <div className={className}>
            <div className={titleClassName} onClick={this.handleClick}>{this.props.title}</div>
            <div className={contentClassName}>{this.props.children}</div>
        </div>;
    }
}



