import * as React from "react";
import * as PropTypes from "prop-types";
import TabsPanel, {TabsPanelProps} from "./TabsPanel";
import { getChildren } from "../ReactUtils";
import NamespaceWrapper, { NamespaceWrapperProps } from "../utils/NamespaceWrapper";

export interface TabsProps extends NamespaceWrapperProps {
    className?: string;
    tabActive?: number;

    onMount?: (tab: number, selectedPanel: any, selectedMenu: any) => void;
    onBeforeChange?: (tab: number, selectedPanel: any, selectedMenu: any) => boolean;
    onAfterChange?: (tab: number, selectedPanel: any, selectedMenu: any) => void;

    position?: "bottom";
    variant?: "loud";
    block?: boolean;

    children?: React.ReactElement<TabsPanelProps>[] | React.ReactElement<TabsPanelProps>;
}

export interface TabsState {
    tabActive?: number;
}

export default class Tabs extends NamespaceWrapper<TabsProps, TabsState> {
    public static displayName = "Tabs";

    public static propTypes = {
        className: PropTypes.string,
        position: PropTypes.oneOf(["bottom"]),
        variant: PropTypes.oneOf(["loud"]),
        block: PropTypes.bool,
        tabActive: PropTypes.number,
        onMount: PropTypes.func,
        onBeforeChange: PropTypes.func,
        onAfterChange: PropTypes.func,
        children: PropTypes.node.isRequired
    };

    constructor(props) {
        super(props);

        this.state = {
            tabActive: props.tabActive
        };
    }

    static defaultProps: TabsProps = {
        tabActive: 1,
        children: []
    };

    public componentDidMount() {
        const index: number = this.state.tabActive;
        const $selectedPanel: any = this.refs["tab-panel"];
        const $selectedMenu: any = this.refs["tab-menu-" + index];
        if (this.props.onMount != null) {
            this.props.onMount(index, $selectedPanel, $selectedMenu);
        }
    }

    public componentWillReceiveProps(nextProps: TabsProps) {
        if (nextProps.tabActive != null) {
            this.setState({tabActive: nextProps.tabActive});
        }
    }

    public render() {
        const className = this.classes(
            "Tabs",
            (this.props.variant == null ? "" : "Tabs--" + this.props.variant),
            (this.props.position == null ? "" : "Tabs--" + this.props.position),
            (this.props.block ? "Tabs--block" : ""),
            (this.props.className == null ? "" : this.props.className)
        );

        return <div className={className}>
            { this.props.position !== "bottom" ? this.renderMenuItems() : null }
            { this.renderPanels() }
            { this.props.position === "bottom" ? this.renderMenuItems() : null }
        </div>;
    }

    public setActive(index: number, e: React.SyntheticEvent<HTMLLIElement>) {
        e.preventDefault();
        e.stopPropagation();

        const onAfterChange = this.props.onAfterChange;
        const onBeforeChange = this.props.onBeforeChange;
        const $selectedPanel = this.refs["tab-panel"];
        const $selectedMenu = this.refs["tab-menu-" + index];

        if (onBeforeChange != null && !onBeforeChange(index, $selectedPanel, $selectedMenu)) {
            return;
        }

        this.setState({tabActive: index}, () => {
            if (onAfterChange != null) {
                onAfterChange(index, $selectedPanel, $selectedMenu);
            }
        });
    }

    private handleKeyDown(index: number, event: React.KeyboardEvent<HTMLLIElement>) {
        let newActive: number = null;
        if (event.keyCode === 37 || event.keyCode === 38) {
            newActive = this.findPreviousActive();
        } else if (event.keyCode === 39 || event.keyCode === 40) {
            newActive = this.findNextActive();
        } else if (event.which === 32) {
            newActive = index;
        }
        if (newActive != null) {
            this.setActive(newActive, event);
        }
    }

    private findNextActive(): number {
        let currentIndex: number = this.state.tabActive - 1;
        currentIndex++;
        while (this.props.children[currentIndex] != null) {
            const panel: TabsPanel = this.props.children[currentIndex];
            if (!panel.props.disabled && !panel.props.hidden) {
                return currentIndex + 1;
            }
            currentIndex++;
        }
        return null;
    }

    private findPreviousActive(): number {
        let currentIndex: number = this.state.tabActive - 1;
        currentIndex--;
        while ((this.props.children[currentIndex] != null)) {
            const panel: TabsPanel = this.props.children[currentIndex];
            if (!panel.props.disabled && !panel.props.hidden) {
                return currentIndex + 1;
            }
            currentIndex--;
        }
        return null;
    }

    private renderPanels() {
        let children: React.ReactElement<TabsPanelProps>[] = getChildren<TabsPanelProps>(this.props.children);
        const that: Tabs = this;

        const $panelsItems: any[] = children.map(($panel, arrIndex, strings) => {
            let panel: React.ReactElement<TabsPanelProps> = $panel;
            const index: number = arrIndex + 1;
            const panelClasses: string = this.classes(
                "Tabs__panel",
                (that.state.tabActive === index) ? "is-display" : "",
                (panel.props.className == null ? "" : panel.props.className)
            );
            return <div ref="tab-panel" key={arrIndex} className={panelClasses} role="tabpanel">{$panel}</div>;
        });

        return $panelsItems;
    }

    private renderMenuItems() {
        if (this.props.children == null) {
            throw new Error("Tabs must contain at least one TabsPanel");
        }

        let children: React.ReactElement<TabsPanelProps>[] = getChildren<TabsPanelProps>(this.props.children);

        const that: Tabs = this;
        const $menuItems: any[] = children.map(($panel, arrIndex, strings) => {
                let panel: React.ReactElement<TabsPanelProps> = $panel;
                const index: number = arrIndex + 1;
                const title: any = panel.props.title;
                const itemClasses: string = this.classes("Tabs__item", (that.state.tabActive === index) ? "is-active" : "", (panel.props.disabled ? "is-disabled" : ""), (panel.props.hidden ? "is-hidden" : ""));
                const parameters: React.LiHTMLAttributes<HTMLLIElement> & {"aria-expanded": string, "aria-selected": string} = {
                    "tabIndex": panel.props.disabled || panel.props.hidden ? -1 : 0,
                    "aria-selected": that.state.tabActive === index ? "true" : "false",
                    "aria-expanded": that.state.tabActive === index ? "true" : "false",
                };
                if (!panel.props.disabled && !panel.props.hidden) {
                    parameters.onClick = (event) => that.setActive(index, event);
                    parameters.onKeyDown = (event) => that.handleKeyDown(index, event);
                }
                if (panel.props.disabled) {
                    parameters["aria-disabled"] = "true";
                }
                if (panel.props.hidden) {
                    parameters["aria-hidden"] = "true";
                }

                parameters.children = title;

                return <li ref={"tab-menu-" + index} key={arrIndex} className={itemClasses} role="tab" {...parameters} />;
            }
        );

        return <ul className={this.classes("Tabs__list")}>{$menuItems}</ul>;
    }
}

export { TabsPanel };



