import * as React from "react";
import { getChildren } from "../ReactUtils";
import NamespaceWrapper, { NamespaceWrapperProps } from "../utils/NamespaceWrapper";

export interface OverlayProps extends NamespaceWrapperProps {
    contentClassName ?: string;

    className ?: string;

    display ?: boolean;

    onHide ?: (event: React.SyntheticEvent<HTMLElement>) => void;

    children ?: React.ReactNode | React.ReactNode[];
}

export default class Overlay extends NamespaceWrapper<OverlayProps, {}> {
    public static displayName = "Overlay";
    static defaultProps: OverlayProps = {
        className: "",
        contentClassName: "",
        display: false
    };

    private renderOverlayContent() {
        const children = getChildren(this.props.children);
        if (children.length === 0) {
            return null;
        }

        return <div className={this.classes("Overlay__content", this.props.contentClassName)}>{this.props.children}</div>;
    }

    public render() {
        return <div className={this.classes("Overlay", this.props.display && "is-active", this.props.className)} onClick={this.props.onHide}>
            {this.renderOverlayContent()}
        </div>;
    }
}



