import * as React from "react";
import { Manager, Popper, Target } from "react-popper";
import Portal from "react-travel";
import ButtonGroup from "./ButtonGroup";

import NamespaceWrapper, { NamespaceWrapperProps } from "../utils/NamespaceWrapper";

export type triggerFunction = (options: { isOpen: boolean, caret: React.ReactNode, arrowProps: object, restProps: object }) => React.ReactNode;

export interface SplitButtonProps extends NamespaceWrapperProps {
  title: React.ReactNode;
  open?: boolean;
  trigger: React.ReactNode | triggerFunction;
}

//

export interface SplitButtonState {
  open?: boolean;
}

export default class SplitButton extends NamespaceWrapper<SplitButtonProps, SplitButtonState> {

  constructor(props) {
    super(props);

    this.state = {
      open: props.open || false
    };
  }

  handleClick = () => {
    if (this.props.open) {
      return;
    }

    this.setState({
      open: !this.state.open
    });
  }

  renderTrigger() {
    if (typeof this.props.trigger !== "function") {
      return this.props.trigger;
    }

    return (options: { arrowProps: object, restProps: object }) => {
      return (this.props.trigger as triggerFunction)({
        arrowProps: options.arrowProps,
        restProps: options.restProps,
        isOpen: this.state.open,
        caret: <span className={this.classes("Caret")}></span>
      });
    };
  }

  render() {
    return <Manager component={ButtonGroup} tabIndex={0}>
        {this.props.title}

        <Target onClick={this.handleClick}>{this.renderTrigger()}</Target>

        {this.state.open && <Portal>
          <Popper placement="bottom-end">
            {this.props.children}
          </Popper>
        </Portal>}
    </Manager>;
  }
}



