import * as React from "react";
import * as PropTypes from "prop-types";
import * as ReactDOM from "react-dom";

export interface RadialProgressProps {
    /**
     * Width and height
     */
    size: number;

    backgroundColor ?: string;

    barColor: string;

    thickness ?: number;

    percent: number;

    withText ?: boolean;

    textColor ?: string;
}

/**
 * @todo Convert to SVG once IE8 is officially out
 */
export default class RadialProgress extends React.Component<RadialProgressProps, {}> {
    public static displayName = "RadialProgress";

    public static propTypes = {//
        size: PropTypes.number.isRequired, //
        backgroundColor: PropTypes.string, //
        barColor: PropTypes.string.isRequired, //
        thickness: PropTypes.number, //
        percent: PropTypes.number.isRequired, //
        withText: PropTypes.bool, //
        textColor: PropTypes.string
    };

    public static defaultProps: RadialProgressProps = {
        size: 0,
        barColor: "",
        percent: 0,

        thickness: 10,
        withText: false,
        backgroundColor: "#ccc"
    };

    public componentDidMount() {
        this.update();
    }

    public componentDidUpdate(prevProps: Readonly<RadialProgressProps>, prevState: Readonly<{}>, prevContext: any) {
        this.update();
    }

    public update() {
        const radius: number = (this.props.size / 2 | 0);
        const canvas: HTMLCanvasElement = ReactDOM.findDOMNode(this.refs["canvas"]) as HTMLCanvasElement;
        const context: CanvasRenderingContext2D = canvas.getContext("2d");
        context.clearRect(0, 0, canvas.width, canvas.height);
        this.renderBackground(context, radius);
        this.renderBar(context, radius);
        if (this.props.withText) {
            this.renderText(context, radius);
        }
    }

    public renderBackground(context: CanvasRenderingContext2D, radius: number) {
        context.fillStyle = this.props.backgroundColor;
        context.beginPath();
        context.arc(radius, radius, radius, 0, Math.PI * 2, false);
        context.arc(radius, radius, radius - this.props.thickness, Math.PI * 2, 0, true);
        context.fill();
    }

    public renderBar(context: CanvasRenderingContext2D, radius: number) {
        context.fillStyle = this.props.barColor;
        const startAngle: number = Math.PI * 1.5;
        const degrees: number = this.props.percent * 3.6;
        const radians: number = degrees * (Math.PI / 180);
        context.beginPath();
        context.arc(radius, radius, radius, startAngle, startAngle + radians, false);
        context.arc(radius, radius, radius - this.props.thickness, startAngle + radians, startAngle, true);
        context.fill();
    }

    public renderText(context: CanvasRenderingContext2D, radius: number) {
        context.fillStyle = this.props.textColor == null ? this.props.barColor : this.props.textColor;
        context.textAlign = "center";
        context.textBaseline = "middle";
        context.font = "bold " + ((radius / 2 | 0)) + "px Arial";
        context.fillText(Math.round(this.props.percent) + "%", radius, radius, 200);
    }

    public render() {
        return <canvas width={this.props.size} height={this.props.size} ref="canvas" />;
    }
}



