import * as React from "react";
import * as PropTypes from "prop-types";

import NamespaceWrapper, { NamespaceWrapperProps } from "../utils/NamespaceWrapper";

export interface NavProps extends React.HTMLAttributes<HTMLDivElement>, NamespaceWrapperProps {
  /**
   * Size of the nav
   */
  size?: "small" | "large";

  horizontal?: boolean;

  hoverable?: boolean;

  loud?: boolean;

  disabled?: boolean;
}

export interface NavItemProps extends NamespaceWrapperProps {
  /**
   * Classes to bind, prefer modifiers
   */
  className?: string;

  linkClassName?: string;

  disabled?: boolean;
  href?: string;
  active?: boolean;

  /**
   * Any component, or the name of the link if "href" is specified
   */
  children?: any;
}

export default class Nav extends NamespaceWrapper<NavProps> {

  static propTypes = {//
    "size": PropTypes.oneOf(["small", "large"]), //
    "horizontal": PropTypes.bool, //
    "hoverable": PropTypes.bool, //
    "loud": PropTypes.bool, //
    "disabled": PropTypes.bool, //
  };

  render() {
    const {namespace, size, horizontal, hoverable, loud, disabled, className, ...props} = this.props;
    const classes = this.classes(
      "Nav",
      size && "Nav--" + size,
      horizontal && "Nav--horizontal",
      hoverable && "Nav--hoverable",
      loud && "Nav--loud",
      disabled && "is-disabled",
      className
    );

    return <div className={classes} {...props} />;
  }
}

export class NavItem extends NamespaceWrapper<NavItemProps> {
  render() {
    const {namespace, href, className, linkClassName, disabled, active, ...props} = this.props;

    // TODO :: find a way to disable links, that isn't the disabled prop on anchor
    return <div className={this.classes("Nav__item", className)}>
      {href ?
        <a href={href} className={this.classes("Nav__link", active && "is-active", disabled && "is-disabled", linkClassName)} {...props} /> : props.children}
    </div>;
  }
}



