import * as React from "react";
import * as ReactDOM from "react-dom";
import NamespaceWrapper, { NamespaceWrapperProps } from "../utils/NamespaceWrapper";

export interface MenuItemProps extends React.LiHTMLAttributes<HTMLLIElement>, NamespaceWrapperProps {
  submenu?: boolean;
  hoverable?: boolean;
  open?: boolean;
  disabled?: boolean;
}

export interface MenuItemState {
  open?: boolean;
}

export default class MenuItem extends NamespaceWrapper<MenuItemProps, MenuItemState> {
  leaveTimeout;

  constructor(props) {
    super(props);

    this.state = {
      open: props.open || false
    };
  }

  componentDidMount() {
    document.addEventListener("click", this.handleClickOutside, true);
  }

  componentWillUnmount() {
    document.removeEventListener("click", this.handleClickOutside, true);
  }

  ignoreEvents() {
    return this.props.open || this.props.disabled || !this.props.submenu;
  }

  handleClickOutside = (e) => {
    if (this.ignoreEvents()) {
      return;
    }

    const el = ReactDOM.findDOMNode(this.refs["container"]);
    if (!el.contains(e.target)) {
      this.setState({open: false});
    }
  }

  handleEnter = () => {
    if (this.ignoreEvents()) {
      return;
    }

    if (this.leaveTimeout) {
      clearTimeout(this.leaveTimeout);
    }

    this.setState({
      open: true
    });
  }

  handleLeave = () => {
    if (this.ignoreEvents()) {
      return;
    }

    // As there is a space between elements
    // we don't want to close the menu instantly
    this.leaveTimeout = setTimeout(() => {
      this.setState({
        open: false
      });
    }, 200);
  }

  handleClick = (e) => {
    if (this.ignoreEvents()) {
      return;
    }

    //Ignore if it comes from an inner menu
    const el = ReactDOM.findDOMNode(this.refs["container"]);
    for (let i = 0; i < el.children.length; i++) {
      if (el.children[i].className.match(/(^| )Menu( |$)/) && el.children[i].contains(e.target)) {
        return;
      }
    }

    this.setState((state) => ({
      open: !state.open
    }));

    if (this.props.onClick) {
      this.props.onClick(e);
    }

    e.stopPropagation();
  }

  getEvents() {
    if (this.props.hoverable) {
      return {
        onMouseEnter: this.handleEnter,
        onMouseLeave: this.handleLeave
      };
    }

    return {
      onClick: this.handleClick
    };
  }

  render() {
    const {namespace, className, submenu, children, hoverable, open, disabled, ...props} = this.props;
    const classes = this.classes("Menu__item", (submenu ? "has-submenu " : ""), this.state.open && "is-open", className);

    return (
      <li ref="container" {...props} {...this.getEvents()} className={classes}>
        {children}{submenu && <span className={this.classes("Caret")} />}
      </li>
    );
  }
}



