import * as React from "react";
import * as PropTypes from "prop-types";

import {Manager, Popper, Target, Arrow} from "react-popper";
import Portal from "react-travel";

import NamespaceWrapper, { NamespaceWrapperProps } from "../utils/NamespaceWrapper";

export interface PopupProps extends NamespaceWrapperProps {
  /**
   * The header of the popup
   */
  title?: React.ReactNode;

  /**
   * The content of the popup
   */
  content: React.ReactNode;

  /**
   * Add a class name to the popup
   */
  className ?: string;

  /**
   * Position of tooltip
   * Can be bottom, top, right, left
   */
  placement ?: "bottom" | "top" | "right" | "left";

  /**
   * Is the tooltip visible by default
   */
  visible ?: boolean;

  /**
   * Hover
   */
  trigger ?: "hover" | "click";
}

export interface PopupState {
  shouldShow?: boolean;
}

export default class Popup extends NamespaceWrapper<PopupProps, PopupState> {
  static propTypes = {
    placement: PropTypes.string,
    title: PropTypes.node,
    content: PropTypes.node.isRequired,
    visible: PropTypes.bool,
    className: PropTypes.string,
    trigger: PropTypes.oneOf(["hover", "click"])
  };

  static defaultProps = {
    placement: "top",
    className: "tooltip",
    trigger: "click"
  };

  constructor (props) {
    super(props);

    this.state = {
      shouldShow: false,
    };
  }

    getActionProps() {
        if (this.props.visible != null) {
            return {};
        }

        if (this.props.trigger === "click") {
            return {
                onClick: () => this.setState({ shouldShow: !this.state.shouldShow })
            };
        }

        return {
            onMouseOver: () => this.setState({ shouldShow: true }),
            onMouseOut: () => this.setState({ shouldShow: false })
        };
  }

  render() {
    return (
        <Manager component="span" style={{display: "inline-block"}}>
            <Target component="span" style={{display: "inline-block"}} {...this.getActionProps()}>{this.props.children}</Target>
            {this._popper()}
        </Manager>
    );
  }

  _popper () {
    if (this.props.visible !== true && (!this.state.shouldShow || this.props.visible === false)) return null;

    const {children, visible, trigger, namespace, title, content, className, ...props} = this.props;

    return (
        <Portal>
            <Popper component="div" className={this.classes("Popup", className)} {...props}>
                {title && <div className={this.classes("Popup__header")}>{title}</div>}
                <div className={this.classes("Popup__content")}>{content}</div>
                <Arrow>
                    {({ arrowProps }) => <span className={this.classes("Popup__arrow")} {...arrowProps} />}
                </Arrow>
            </Popper>
        </Portal>
    );
  }
}



