import * as React from "react";
import * as PropTypes from "prop-types";

import NamespaceWrapper, { NamespaceWrapperProps } from "../utils/NamespaceWrapper";

export interface ButtonProps extends React.ButtonHTMLAttributes<HTMLElement>, React.AnchorHTMLAttributes<HTMLElement>, NamespaceWrapperProps {
    /**
     * Set the size of the button
     */
    size?: "xsmall" | "small"| "large";

    /**
     * Set the color of the button
     */
    level?: "info" | "warning" | "error" | "success";

    /**
     * Set the variant of the button
     */
    variant?: "primary" | "secondary" | "cta" | "text" | "flat";

    /**
     * Disable the button
     */
    disabled?: boolean;

    /**
     * Active / Focused state
     */
    active?: boolean;

    /**
     * Is loading
     */
    loading?: boolean;

    block?: boolean;
}

export default class Button extends NamespaceWrapper<ButtonProps, {}> {
    public static displayName = "Button";

    public static propTypes = {//
        "type": PropTypes.string, //
        "href": PropTypes.string, //

        // Modifiers
        "className": PropTypes.string, //
        "level": PropTypes.oneOf(["info", "warning", "error", "success"]), //
        "size": PropTypes.oneOf(["xsmall", "small", "large"]), //
        "variant": PropTypes.oneOf(["primary", "secondary", "cta", "text", "flat"]), //
        "block": PropTypes.bool, //

        // States
        "disabled": PropTypes.bool, //
        "active": PropTypes.bool, //
        "loading": PropTypes.bool, //

        // Handlers
        "onClick": PropTypes.func, //
        "onBlur": PropTypes.func
    };

    public render() {
        const {variant, level, size, loading, block, active, ...props}: ButtonProps = this.props;

        if (loading || props.disabled) {
          props.tabIndex = -1;
        }

        if (props.disabled) {
          props["aria-disabled"] = "true";
        }

        props.className = this.classes(
          "Button",
          variant && "Button--" + variant,
          size && "Button--" + size,
          level && "Button--" + level,
          block && "Button--block",
          active && "is-active",
          loading && "is-loading",
          props.disabled && "is-disabled",
          this.props.className);

        return (props.href)
          ? <a role="button" {...props as React.AnchorHTMLAttributes<HTMLElement>} />
          : <button {...props as React.ButtonHTMLAttributes<HTMLElement>} />;
    }
}



