import * as React from "react";
import NamespaceWrapper, { NamespaceWrapperProps } from "../utils/NamespaceWrapper";
import { IconClose } from "sq-web-icons";
import Portal from "react-travel";

export interface DialogProps extends React.HTMLAttributes<HTMLDivElement>, NamespaceWrapperProps {
  open?: boolean;

  backdrop?: boolean;

  children?: ((close: React.ReactNode) => React.ReactNode) | React.ReactNode;

  onClose?: () => void;
}

export default class Dialog extends NamespaceWrapper<DialogProps, {}> {
  handleClose = () => {
    this.props.onClose();
  }

  renderChildren(children) {
    if (typeof children === "function") {
      return children(
        <span className={this.classes("Dialog__close")}
              onClick={this.handleClose}>
        <IconClose />
      </span>
      );
    }

    return children;
  }

  render() {
    const {namespace, className, backdrop, open, children, onClose, ...props} = this.props;
    const classes = this.classes(
      "Dialog",
      "is-open",
      (backdrop && "has-backdrop"),
      className
    );
    return open ? <Portal>
      <div className={classes} aria-hidden="false" role="dialog" {...props}>
        <div className={this.classes("Dialog__container")}
             role="document">
          {this.renderChildren(children)}
        </div>
      </div>
    </Portal> : <span />;
  }
}

export interface DialogHeaderProps extends React.HTMLAttributes<HTMLDivElement>, NamespaceWrapperProps {
}

export class DialogHeader extends NamespaceWrapper<DialogHeaderProps> {
  render() {
    const {namespace, className, ...props} = this.props;
    return <div
      className={this.classes("Dialog__header", className)} {...props} />;
  }
}

export interface DialogFooterProps extends React.HTMLAttributes<HTMLDivElement>, NamespaceWrapperProps {
}

export class DialogFooter extends NamespaceWrapper<DialogFooterProps> {
  render() {
    const {namespace, className, ...props} = this.props;
    return <div
      className={this.classes("Dialog__footer", className)} {...props} />;
  }
}

export interface DialogContentProps extends React.HTMLAttributes<HTMLDivElement>, NamespaceWrapperProps {
}

export class DialogContent extends NamespaceWrapper<DialogContentProps> {
  render() {
    const {namespace, className, ...props} = this.props;
    return <div className={this.classes("Dialog__content", className)} {...props} />;
  }
}



