import * as React from "react";

import NamespaceWrapper, { NamespaceWrapperProps } from "../utils/NamespaceWrapper";

export interface FormItemProps extends NamespaceWrapperProps {
  /**
   * The label to display with the form
   */
  label ?: React.ReactNode;

  /**
   * The error message to display
   */
  errorMessage ?: React.ReactNode;

  /**
   * Append a class name
   */
  className ?: string;

  /**
   * Append a class name on Form__item__control
   */
  controlClassName ?: string;

  /**
   * The "for" attribute
   */
  htmlFor ?: string;

  /**
   * Does this element have addons ?
   */
  addons ?: boolean;

  /**
   * Does this element have icons ?
   */
  icons ?: boolean;

  /**
   * Is the field required ?
   */
  required ?: boolean;

  /**
   * Is there an error ? becomes true when an errorMessage is set
   */
  hasError ?: boolean;
}

export default class FormItem extends NamespaceWrapper<FormItemProps, {}> {
  public static displayName = "FormItem";

  public render() {
    const hasError = this.props.hasError || this.props.errorMessage;
    const className = this.classes("Form__item", this.props.className, this.props.required && "is-required", hasError && "has-error");
    const controlClassName = this.classes("Form__item__control", this.props.controlClassName, this.props.addons && "has-addon", this.props.icons && "has-icon");

    return <div className={className}>
      <div className={this.classes("Form__item__container")}>
        <label className={this.classes("Form__item__label")}
               htmlFor={this.props.htmlFor}>{this.props.label}</label>
        <div className={controlClassName}>
          {this.props.children}
        </div>
      </div>
      {this.props.errorMessage && (
        <div
          className={this.classes("Form__item__message")}>
          {this.props.errorMessage}
        </div>
      )}
    </div>;
  }
}



