import * as React from "react";
import * as PropTypes from "prop-types";

import NamespaceWrapper, { NamespaceWrapperProps } from "../utils/NamespaceWrapper";

export interface InputProps extends NamespaceWrapperProps /*, React.InputHTMLAttributes<HTMLInputElement>*/ {
    /**
     * set the value of the field.
     */
    value ?: string;

    /**
     * set the default value. (will only be set at component mount)
     */
    defaultValue ?: string;

    /**
     * set a placeholder on the field
     */
    placeholder ?: string;

    /**
     * set an ID on the field
     */
    id ?: string;

    /**
     * The field's type
     */
    type ?: string;

    /**
     * The field's name
     */
    name ?: string;

    /**
     * The field's maxLength
     */
    maxLength ?: number;

    /**
     * Triggered when a value is changed
     */
    onChange ?: (event: React.ChangeEvent<HTMLInputElement>) => void;

    onFocus ?: (event: React.ChangeEvent<HTMLInputElement>) => void;

    onBlur ?: (event: React.ChangeEvent<HTMLInputElement>) => void;

    onKeyDown ?: (event: React.ChangeEvent<HTMLInputElement>) => void;

    /**
     * Disable the input
     */
    disabled ?: boolean;

    /**
     * Set the input as readOnly
     */
    readOnly ?: boolean;

    /**
     * Add a class to the container, prefer modifiers
     */
    className ?: string;

    /**
     * set the size
     */
    size ?: "small"| "large";
}

export interface InputState {
    /**
     * determines whether placeholder must be shown or not
     */
    placeholderActive ?: boolean;
}

const ESC_KEY_CODE = 27;

export default class Input extends NamespaceWrapper<InputProps, InputState> {
    public static displayName = "Input";

    public static propTypes = {//
        "id": PropTypes.string, //
        "value": PropTypes.string, //
        "defaultValue": PropTypes.string, //
        "placeholder": PropTypes.string, //
        "type": PropTypes.string, //
        "name": PropTypes.string, //
        "maxLength": PropTypes.number, //

        "disabled": PropTypes.bool, //
        "readOnly": PropTypes.bool, //

        "onChange": PropTypes.func, //
        "onFocus": PropTypes.func, //
        "onBlur": PropTypes.func, //
        "onKeyDown": PropTypes.func, //

        "className": PropTypes.string, //
        "size": PropTypes.oneOf(["small", "large"]) //
    };

    private wrappedInput: HTMLInputElement;
    private placeholderSupport: boolean;
    private needsManualPlaceholding: boolean;

    constructor(props) {
        super(props);

        this.state = {
            placeholderActive: false
        };

        this.placeholderSupport = ("placeholder" in document.createElement("input"));
        this.needsManualPlaceholding = this.props.placeholder && !this.placeholderSupport;
    }

    render() {
        const {className, size, ...props} = this.props;
        const classes = this.classes("Input", (size && "Input--" + size), className);

        if (props.readOnly || props.disabled) {
            props["tabIndex"] = -1;
        }

        if (props.disabled) {
            props["aria-disabled"] = "true";
        }

        return (<div className="u-posRelative">
            {this.renderPlaceholder()}
            <input className={classes}
                   {...props}
                   onFocus={this.handleFocus}
                   onBlur={this.handleBlur}
                   onKeyDown={this.handleKeyDown}
                   ref={(input) => { this.wrappedInput = input; }}
            />
        </div>);
    }

    componentWillReceiveProps(nextProps: InputProps) {
        this.needsManualPlaceholding = !this.placeholderSupport && !!nextProps.placeholder;
    }

    componentDidMount() {
        if (this.needsManualPlaceholding) {
            setTimeout(() => this.setPlaceholderActive(this.wrappedInput && !this.wrappedInput.value), 0);
        }
    }

    private renderPlaceholder() {
        if (this.needsManualPlaceholding && this.state.placeholderActive) {
            return <div className={this.classes("Input__placeholder")} onClick={this.handlePlaceholderClick}>{this.props.placeholder}</div>;
        }
        return null;
    }

    private handlePlaceholderClick = () => {
        this.wrappedInput.focus();
    }

    private handleFocus = (event) => {
        if (this.props.onFocus) {
            this.props.onFocus(event);
        }
        this.setPlaceholderActive(false);
    }

    private handleBlur = (event) =>  {
        this.setPlaceholderActive(!this.wrappedInput.value);
        if (this.props.onBlur) {
            this.props.onBlur(event);
        }
    }

    private setPlaceholderActive(active: boolean) {
        if (this.needsManualPlaceholding) {
            this.setState({
                placeholderActive: active
            });
        }
    }

    // prevent IE from wiping data out from input and form
    private handleKeyDown = (event) =>  {
        if (this.props.onKeyDown) {
            this.props.onKeyDown(event);
        }

        if (event.keyCode === ESC_KEY_CODE) {
            event.preventDefault();
            event.stopPropagation();
        }
    }
}



