import * as React from "react";
import * as PropTypes from "prop-types";

import NamespaceWrapper, { NamespaceWrapperProps } from "../utils/NamespaceWrapper";

export interface CheckboxProps extends NamespaceWrapperProps {
    /**
     * Name of the field
     */
    name ?: string;

    /**
     * Is it checked by default ?
     */
    defaultChecked ?: boolean;

    /**
     * Is it currently checked
     */
    checked ?: boolean;

    /**
     * Triggered on any change
     */
    onChange ?: (checked: boolean, event: React.SyntheticEvent<HTMLInputElement>) => void;

    /**
     * Disables the switch
     */
    disabled ?: boolean;

    /**
     * Add a class name
     */
    className ?: string;

    /**
     * TODO :: rename to switch
     */
    isSwitch ?: boolean;
}

export default class Checkbox extends NamespaceWrapper<CheckboxProps, {}> {
    public static displayName = "Checkbox";

    constructor(props: CheckboxProps) {
        super(props);

        this.handleChange = this.handleChange.bind(this);
    }

    public static propTypes = {//
        "name": PropTypes.string, //
        "defaultChecked": PropTypes.bool, //
        "checked": PropTypes.bool, //
        "onChange": PropTypes.func, //
        "disabled": PropTypes.bool, //
        "className": PropTypes.string, //
        "isSwitch": PropTypes.bool //
    };

    public render() {
        const classes = this.classes(
          "Checkbox",
          this.props.isSwitch && "Checkbox--switch",
          this.props.className
        );

        return <label className={classes}>
            {this.renderInput()}
            <span className={this.classes("Checkbox__label")}>
                {this.props.children}
            </span>
        </label>;
    }

    public renderInput() {
        const options: React.InputHTMLAttributes<HTMLInputElement> = {};
        if (this.props.name != null) {
            options.name = this.props.name;
        }
        if (this.props.checked != null) {
            options.checked = this.props.checked;
        }
        if (this.props.defaultChecked != null) {
            options.defaultChecked = this.props.defaultChecked;
        }
        return <input onChange={this.handleChange} type="checkbox" value="1" disabled={!!this.props.disabled} {...options} />;
    }

    private handleChange(event: React.ChangeEvent<HTMLInputElement>) {
        if (this.props.onChange != null) {
            this.props.onChange(event.target.checked, event);
        }
    }
}



