import * as React from "react";
import * as PropTypes from "prop-types";

import NamespaceWrapper, { NamespaceWrapperProps } from "../utils/NamespaceWrapper";
import {InputProps, default as Input} from "./Input";
import Button, {ButtonProps} from "./Button";

import {IconSearch} from "sq-web-icons";

export interface SearchProps extends InputProps, NamespaceWrapperProps {
    /**
     * Triggers action
     */
    onClick ?: (event: React.SyntheticEvent<HTMLElement>) => void;
}

export interface SearchState {
    value ?: string;
}

export default class Search extends NamespaceWrapper<SearchProps, SearchState> {
    public static displayName = "Search";

    public static propTypes = {//
        // Field props
        "id": PropTypes.string, //
        "value": PropTypes.string, //
        "defaultValue": PropTypes.string, //
        "placeholder": PropTypes.string, //
        "type": PropTypes.string, //
        "name": PropTypes.string, //
        "maxLength": PropTypes.number, //
        "disabled": PropTypes.bool, //
        "readOnly": PropTypes.bool, //
        "onChange": PropTypes.func, //
        "size": PropTypes.oneOf(["small", "large"]), //

        // Search props
        "onClick": PropTypes.func, //
        "className": PropTypes.string, //
    };

    constructor(props) {
        super(props);

        this.state = {
            value: this.props.value || this.props.defaultValue
        };

        this.handleChange = this.handleChange.bind(this);
        this.handleClear = this.handleClear.bind(this);
    }

    componentWillReceiveProps(nextProps: SearchProps) {
        if (this.props.value !== nextProps.value) {
            this.setState({value: this.props.value});
        }
    }

    handleChange(event) {
        if (this.props.onChange) {
            this.props.onChange(event);
        }

        this.setState({value: event.target.value});
    }

    handleClear(event) {
        const {...eventClone} = event;
        this.setState({value: ""}, () => {
            this.handleChange(eventClone);
        });
    }

    render() {
        const {onClick, onChange, className, children, ...inputProps} = this.props;

        const searchClassName = this.classes(
            "Search",
            (this.props.size ? "Search--" + this.props.size : ""),
            (this.props.disabled ? "is-disabled" : ""),
            (this.props.onClick ? "has-action" : ""),
            (this.state.value ? "has-text" : ""),
            (className ? className : "")
        );

        const buttonConfig: ButtonProps = {variant: "text"};

        if (this.props.size) {
            buttonConfig.size = this.props.size;
        }

        if (this.props.disabled) {
            buttonConfig.disabled = this.props.disabled;
        }

        return (
            <form className={searchClassName}>
                <span className={this.classes("Search__input")}>
                    <Input type="search" onChange={this.handleChange} {...inputProps} />
                </span>
                <span className={this.classes("Search__clear")}>
                    <Button type="reset" onClick={this.handleClear} {...buttonConfig} children={"×"} />
                </span>
                {this.props.onClick && (
                    <span className={this.classes("Search__action")}>
                        <Button onClick={this.props.onClick} {...buttonConfig} children={<IconSearch />} />
                    </span>
                )}
            </form>
        );
    }
}



