import * as React from "react";
import * as ReactDOM from "react-dom";
import { Manager, Popper, Target, IPopperProps } from "react-popper";
import Portal from "react-travel";

import clickOutside from "../utils/clickOutside";
import NamespaceWrapper, {
  NamespaceWrapperProps
} from "../utils/NamespaceWrapper";
import { isIE9OrBelow, isRTL } from "../utils";

export interface DropdownProps extends NamespaceWrapperProps {
  disabled?: boolean;
  up?: boolean;
  open?: boolean;
  block?: boolean;
  size?: "small" | "medium" | "large";
  className?: string;
  trigger:
    | ((isOpen: boolean, caret: React.ReactNode) => React.ReactNode)
    | React.ReactNode;

  style?: React.CSSProperties;
}

export interface DropdownState {
  open?: boolean;
}

export default class Dropdown extends NamespaceWrapper<
  DropdownProps,
  DropdownState
> {
  outsideTap;
  manager: Element;
  popper: Element;
  target: Element;

  constructor(props) {
    super(props);

    this.state = {
      open: !!props.open || false
    };
  }

  componentDidMount() {
    this.setOusideTap();
  }

  componentDidUpdate(lastProps, lastState) {
    if (lastState.open !== this.state.open) {
      setTimeout(() => this.setOusideTap());
    }
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.open != nextProps.open) {
      this.setState(() => ({open: !!nextProps.open}));
    }
  }

  componentWillUnmount() {
    this.outsideTap.remove();
  }

  private setOusideTap = () => {
    const elements = [this.target];

    if (this.popper) {
      elements.push(this.popper);
    }

    if (this.outsideTap) {
      this.outsideTap.remove();
    }

    this.outsideTap = clickOutside(
      elements,
      ["click", "touchstart"],
      this.handleOutsideTap
    );
  }

  handleOutsideTap = () => {
    this.setState({ open: false });
  }

  handleClick = event => {
    if (this.props.open && this.state.open) {
      return;
    }

    this.setState(oldState => ({
      open: !oldState.open
    }));
    event.preventDefault();
  }

  renderTrigger() {
    if (typeof this.props.trigger === "function") {
      return this.props.trigger(
        this.state.open,
        <span className={this.classes("Caret")} />
      );
    }

    return this.props.trigger;
  }

  getPlacement(
    up: boolean
  ): "top-end" | "bottom-end" | "top-start" | "bottom-start" {
    if (this.manager && isRTL(this.manager)) {
      return up ? "top-end" : "bottom-end";
    } else {
      return up ? "top-start" : "bottom-start";
    }
  }

  render() {
    const {
      namespace,
      disabled,
      open,
      size,
      up,
      trigger,
      children,
      className,
      block,
      ...props
    } = this.props;

    const classes = this.classes(
      "Dropdown",
      disabled && "is-disabled",
      this.state.open && "is-open",
      size && "Dropdown--" + size,
      up && "Dropdown--dropup",
      block && "Dropdown--block",
      isIE9OrBelow() && "Dropdown--legacy",
      className
    );

    let ManagerComponent = isIE9OrBelow() ? "div" : Manager;
    let TargetComponent = isIE9OrBelow() ? "div" : Target;
    let targetProps: {innerRef?: (element) => void, ref?: (element) => void} = {innerRef: c => (this.target = ReactDOM.findDOMNode(c))};

    if (isIE9OrBelow()) {
      targetProps.ref = targetProps.innerRef;
      delete targetProps.innerRef;
    }

    // TODO :: report "block" and "size" to the child popper automatically

    return (
      <ManagerComponent
        className={classes}
        tabIndex={0}
        ref={manager => (this.manager = ReactDOM.findDOMNode(manager))}
        {...props}
      >
        <TargetComponent
          className={this.classes("Dropdown__trigger")}
          onClick={this.handleClick}
          {...targetProps}
        >
          {this.renderTrigger()}
        </TargetComponent>

        {this.renderPopper(children, up)}
      </ManagerComponent>
    );
  }

  renderPopper(children, up) {
    if (!this.state.open) {
      return;
    }

    if (isIE9OrBelow()) {
      return (
        <div
          className={this.classes("Dropdown__popper")}
          ref={c => (this.popper = c)}
        >
          {children}
        </div>
      );
    }

    return (
      <Portal>
        <Popper
          className={this.classes("Dropdown__popper")}
          placement={this.getPlacement(up)}
          innerRef={c => {
            this.popper = ReactDOM.findDOMNode(c);
          }}
        >
          {children}
        </Popper>
      </Portal>
    );
  }
}



