import * as React from "react";
import * as PropTypes from "prop-types";

import NamespaceWrapper, { NamespaceWrapperProps } from "../utils/NamespaceWrapper";

export interface BadgeProps extends NamespaceWrapperProps {
    /**
     * Set a class for the badge, prefer modifiers
     */
    className?: string;

    /**
     * Set the size of the button
     */
    size ?: "small" | "large";

    /**
     * Set the color of the button
     */
    level ?: "info" | "warning" | "error" | "success";

    /**
     * Show badge as a pill
     */
    pill ?: boolean;
}

export default class Badge extends NamespaceWrapper<BadgeProps, {}> {
    public static displayName = "Badge";
    public static propTypes = {//
        // Modifiers
        "className": PropTypes.string, //
        "level": PropTypes.oneOf(["info", "warning", "error", "success"]), //
        "size": PropTypes.oneOf(["small", "large"]), //

        // States
        "pill": PropTypes.bool, //

        // Deprecated
        "color": PropTypes.string, //
        "text": PropTypes.string, //
        "boldTextPositionFromEnd": PropTypes.number, //
        "boldTextLength": PropTypes.number //
    };

    public render() {
        const className = this.classes(
          "Badge",
          (this.props.level && "Badge--" + this.props.level),
          (this.props.size && "Badge--" + this.props.size),
          (this.props.pill && "Badge--pill"),
          this.props.className
        );
        return <span className={className}>
            {this.props.children}
            </span>;
    }
}



