import * as React from "react";
import * as PropTypes from "prop-types";

import NamespaceWrapper, { NamespaceWrapperProps } from "../utils/NamespaceWrapper";

export interface ListProps extends React.HTMLAttributes<HTMLUListElement>, NamespaceWrapperProps {
  size?: "small" | "large";
  horizontal?: boolean;
  divided?: boolean;
}

export default class List extends NamespaceWrapper<ListProps> {

  static propTypes = {
    className: PropTypes.string,
    size: PropTypes.string,
    // variant: PropTypes.string, //FIXME: gives a validation error in TypeScript for whatever reason
    divided: PropTypes.bool,
    children: PropTypes.node.isRequired
  };

  render() {
    const {namespace, divided, size, horizontal, className, ...props} = this.props;
    const classes = this.classes(
      "List",
      divided && "has-divider",
      size && "List--" + size,
      horizontal && "List--horizontal",
      className
    );

    return <ul className={classes} {...props} />;
  }
}

export interface ListItemProps extends React.LiHTMLAttributes<HTMLLIElement>, NamespaceWrapperProps {
  divided?: boolean;
  hasSublist?: boolean;
}

export class ListItem extends NamespaceWrapper<ListItemProps> {
  static propTypes = {
    className: PropTypes.string,
    divided: PropTypes.bool,
    hasSublist: PropTypes.bool
  };

  render() {
    const {namespace, divided, hasSublist, className, ...props} = this.props;
    const classes = this.classes("List__item", divided && "has-divider", hasSublist && "has-sublist", className);

    return <li className={classes} {...props} />;
  }
}



