import * as React from "react";

import NamespaceWrapper, { NamespaceWrapperProps } from "../utils/NamespaceWrapper";

export interface Column {
    label: string;

    renderer?: (cellValue: String, rowIndex: String) => any;

    key: string;
}

export interface TableProps extends NamespaceWrapperProps {
    cols: { [key: string]: Column };

    data: { [key: string]: any}[];

    className ?: string;
}

export default class Table extends NamespaceWrapper<TableProps, {}> {
    public static displayName = "Table";

    private renderTableHead() {
        const columns = [];

        for (let j in this.props.cols) {
            if (!this.props.cols.hasOwnProperty(j)) {
                continue;
            }

            columns.push(<th>{this.props.cols[j].label}</th>);
        }

        return <thead>
        <tr>{columns}</tr>
        </thead>;
    }

    private renderTableBody() {
        const rows = [];
        for (const i in this.props.data) {
            if (!this.props.data.hasOwnProperty(i)) {
                continue;
            }

            const row = this.props.data[i];
            const columns = [];
            for (let columnIndex in this.props.cols) {
                if (!this.props.cols.hasOwnProperty(columnIndex)) {
                    continue;
                }

                const columnRenderFunction = this.props.cols[columnIndex].renderer;
                const columnKey: string = this.props.cols[columnIndex].key;
                const cellValue: string = row[columnKey];

                if (columnRenderFunction != null) {
                    columns.push(<td>{columnRenderFunction(cellValue, i)}</td>);
                } else {
                    columns.push(<td>{cellValue}</td>);
                }

            }

            rows.push(<tr>{columns}</tr>);
        }

        return <tbody>{rows}</tbody>;
    }

    public render() {
        return <table className={this.classes("Table", this.props.className)}>
          {this.renderTableHead()}
          {this.renderTableBody()}
        </table>;
    }
}



